@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* btn-def */
.btn-def {
	$btn-font-size: 1.4rem;
	$btn-line-height: 1.8rem;
	$btn-letter-spacing: 0.2em;
	$btn-border-width: 0.2rem;
	$btn-space-inner-vertical: round-float(($btn-height - $btn-line-height - $btn-border-width * 2) / 2, 1);
	$btn-space-inner-horizontal: $btn-space-inner-vertical * 2;
	display: inline-block;
	vertical-align: top;
	position: relative;
	margin: 0;
	padding: #{$btn-space-inner-vertical} #{$btn-space-inner-horizontal};
	color: $btn-text-color;
	text-transform: uppercase;
	text-decoration: none;
	text-align: center;
	cursor: pointer; // Для input-елементов
	border: $btn-border-width solid transparent;
	background-color: $btn-bg-color;
	box-shadow: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	&, & > span {
		@include font-size($btn-font-size, $btn-line-height);
		letter-spacing: $btn-letter-spacing; }

	// Hover
	&:hover {
		.no-touchevents & {
			background-color: $btn-bg-hover-color; } }

	// Disabled
	&:disabled, &.disabled, &[data-disabled] {
		@include btn-disabled(); } }

/* btn-def end */

/* btn-alt */
.btn-alt {
	$btn-font-size: 1.4rem;
	$btn-line-height: 1.8rem;
	$btn-letter-spacing: 0.2em;
	$btn-border-width: 0.2rem;
	$btn-space-inner-vertical: round-float(($btn-height - $btn-line-height - $btn-border-width * 2) / 2, 1);
	$btn-space-inner-horizontal: $btn-space-inner-vertical * 2;
	display: inline-block;
	vertical-align: top;
	position: relative;
	margin: 0;
	padding: #{$btn-space-inner-vertical} #{$btn-space-inner-horizontal};
	color: $btn-bg-color;
	text-transform: uppercase;
	text-decoration: none;
	text-align: center;
	cursor: pointer; // Для input-елементов
	border: $btn-border-width solid $btn-bg-color;
	background-color: white;
	box-shadow: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	&, & > span {
		@include font-size($btn-font-size, $btn-line-height);
		letter-spacing: $btn-letter-spacing; }

	// Hover
	&:hover {
		.no-touchevents & {
			color: $btn-text-color;
			background-color: $btn-bg-color; } }

	// Disabled
	&:disabled, &.disabled, &[data-disabled] {
		@include btn-disabled(); } }

/* btn-alt end */

/* button toggle menu */
.btn-menu {
	$btn-menu-bg: transparent;
	$btn-menu-color: $base-color-alt;
	$btn-menu-lines-width: 24px;
	$btn-menu-lines-height: 3px;
	$btn-menu-lines-space: 5px;
	$btn-menu-lines-color: currentColor;
	$btn-menu-animation-speed: $animation-speed;
	$btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

	$user-options-cross-width: $btn-menu-lines-width;
	$user-options-cross-height: $btn-menu-lines-height;
	$user-options-cross-color: $text-color;

	// =====================

	display: none;
	@include size($header-height);
	position: fixed;
	left: 0;
	top: 0;
	text-decoration: none;
	color: $btn-menu-color;
	background-color: $btn-menu-bg;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	z-index: 99;
	transition: {
		property: background-color;
		duration: $animation-speed; }

	@include media('<tab') {
		display: block; }

	.touchevents & {
		// убрать подсветку при клике на тачскринах
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	& > span {
		@include hide-text;
		white-space: normal; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include size($user-options-cross-width, $user-options-cross-height);
		background-color: $user-options-cross-color;
		@include position(50%);
		@include margin($top: -#{round($user-options-cross-height*0.5)}, $left: -#{round($user-options-cross-width*0.5)});
		will-change: opacity, transform;
		opacity: 0;
		transition: {
			property: transform, opacity;
			duration: $btn-menu-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(13px, $btn-menu-lines-height);
		background-color: $btn-menu-lines-color;
		@include margin($top: -#{round($btn-menu-lines-height*0.5)}, $left: -#{round($btn-menu-lines-width*0.5)});
		border-radius: $btn-menu-lines-height;
		backface-visibility: hidden;
		transition: {
			property: opacity;
			duration: $btn-menu-animation-speed;
			delay: $btn-menu-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size($btn-menu-lines-width, $btn-menu-lines-height);
			border-radius: $btn-menu-lines-height;
			background-color: $btn-menu-lines-color;
			backface-visibility: hidden;
			transition: {
				property: transform;
				duration: $btn-menu-animation-speed;
				delay: $btn-menu-animation-delay; } }

		//first line menu
		&::before {
			top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

		//third line menu
		&::after {
			transform: scale(round-float(18/24, 2), 1);
			transform-origin: left center;
			top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

	// hover
	.no-touchevents &:hover {
		box-shadow: inset 0 -2px 0 $base-color; }

	// active
	&.active {
		&::before, &::after {
			transition-delay: $btn-menu-animation-delay;

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } }

		.no-touchevents &:hover {
			box-shadow: none;

			&::before, &::after {
				background-color: $base-color; } } } }

/* button toggle menu end */
