@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* helper classes */
.mark {
  color: $mark-color; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.push-auto {
  margin-left: auto;
  margin-right: auto; }

.nobr {
  white-space: nowrap; }

.hide {
  display: none !important; }

.hide-text {
  @include hide-text; }

.desktop-only {
  @include media('<tab') {
    display: none; } }

.devices-only {
  @include media('>=tab') {
    display: none; } }

.mobile-only {
  @include media('>=mob') {
    display: none; } }

/* ==================================== */
/* ======= NON CONTENT ELEMENTS ======= */
/* ==================================== */
/* shutter overlay */
.shutter-overlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: black;
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  transition: {
    property: opacity, visibility;
    duration: $animation-speed;
    timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

  &.active {
    visibility: visible;
    opacity: 0.35;

    // скрывать на десктопе, если shutter-only-mob
    .shutter-only-mob & {
      @include media('>=tab') {
        display: none; } } } }

/* shutter overlay end */

/* btn-to-top */
.btn-to-top {
  $btn-tt-bg-color: $text-color;
  //==============
  position: fixed;
  right: 0;
  top: 50%;
  width: nth($layout-page-space, 2);
  @include font-light;
  @include font-size(2.4rem, round-float(24/24, 2));
  color: #5a534f;
  text-align: center;
  text-decoration: none;
  z-index: 10;
  // под хедером, но над контентом и футером
  $prop: background-color, opacity, visibility;
  will-change: $prop;
  opacity: 0;
  visibility: hidden;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<lap-s') {
    width: nth($layout-page-space--md, 2);
    font-size: 1.8rem; }

  @include media('<tab') {
    display: none; }

  span {
    display: block; }

  svg {
    transform: rotate(0.5turn);
    display: inline-block;
    vertical-align: top; }

  path {
    fill: currentColor; }

  &.btn-to-top--show {
    opacity: 1;
    visibility: visible; }

  .no-touchevents &:hover {
    color: $base-color; } }

/* btn-to-top end */

/* ==================================== */
/* ============== HEADER ============== */
/* ==================================== */
.header {
  margin: 0;
  //position: sticky
  //top: 0
  //z-index: 15
  z-index: 98;
  transition: {
    property: background-color;
    duration: $animation-speed; }

  .layout-page-spaces {
    @include media('<tab') {
      padding: 0; } } }

.header-float {
  width: 100%;
  min-width: 320px;
  background-color: #403b38;
  z-index: inherit;

  .header-is-top & {
    position: fixed;
    left: 0;
    top: 0; }

  .shutter-after-open & {
    @include media('<tab') {
      background-color: $base-color-light; } } }

.header__holder {
  position: relative;
  text-align: center;
  height: $header-height;
  padding: 0 nth($logo-size, 2) + 1;
  z-index: 5;

  @include media('<tab') {
    padding: 0 $header-height; } }

/* logo */
.logo {
  position: absolute;
  left: 0;
  top: 50%;
  width: nth($logo-size, 2);
  margin: -#{round-float(nth($logo-size, 1) / 2, 1)} 0 0;
  text-indent: -9999px;
  text-align: left;
  overflow: hidden;
  z-index: 5;

  @include media('<tab') {
    width: nth($logo-short-size, 2);
    margin: -#{round-float(nth($logo-short-size, 1)/2, 1)} 0 0 -#{round-float(nth($logo-short-size, 2)/2, 1)};
    left: 50%; }

  &::before, &::after {
    content: "";
    display: block;
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: opacity;
      duration: $animation-speed; } }

  &::before {
    @include ratio(nth($logo-size, 2), nth($logo-size, 1));
    background: url(../../img/logo.png) no-repeat;

    @include media('<tab') {
      @include ratio(nth($logo-short-size, 2), nth($logo-short-size, 1));
      background: none; }

    .header-is-top & {
      opacity: 0; } }

  &::after {
    position: absolute;
    left: 50%;
    top: 50%;
    opacity: 0;
    @include size(nth($logo-short-size, 2), nth($logo-short-size, 1));
    background: url(../../img/logo-short.png) no-repeat;
    margin: -#{round-float(nth($logo-short-size, 1) / 2 - 0.1, 1)} 0 0 -#{round-float(nth($logo-short-size, 2) / 2 - 0.1, 1)};

    @include media('<tab') {
      opacity: 1;
      margin: -#{round-float(nth($logo-short-size, 1) / 2, 1)} 0 0 -#{round-float(nth($logo-short-size, 2) / 2, 1)}; }

    .header-is-top & {
      opacity: 1; } }

  a, & > span {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 100%;
    display: block;
    z-index: 1;
    transform: translate(0, -50%);
    will-change: transform;

    .header-is-top & {
      height: nth($logo-short-size, 1); } } }

/* logo end */

/* ==================================== */
/* ============ NAVIGATION ============ */
/* ==================================== */
.nav-wrap, .nav-footer {
  &::before {
    position: absolute;
    content: "";
    display: none;
    top: 0;
    left: 20px;
    right: 20px;
    border: solid #cfb9aa;
    border-width: 0;

    @include media('<tab') {
      display: block; } } }

.nav-wrap {
  display: inline-block;
  // для ие
  vertical-align: top;
  // для ие

  @include media('<tab') {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% - #{$header-height});
    background-color: $base-color-light;
    opacity: 0;
    visibility: hidden;
    transition: {
      property: opacity, visibility;
      duration: $animation-speed; } }

  &.active {
    @include media('<tab');
    @include media('<tab') {
      opacity: 1;
      visibility: visible; } }

  &::before {
    border-bottom-width: 1px; } }

.nav {
  position: relative;
  width: 100%;

  @include media('<tab') {
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center; } }

.nav__list {
  @include clear;

  @include media('<tab') {
    width: 100%;
    max-height: 100%;
    overflow-x: hidden;
    overflow-y: auto; }

  li {
    float: left;

    @include media('<tab') {
      float: none;
      display: block; } }

  a {
    padding: 10px 30px;
    display: block;
    display: flex;
    align-items: center;
    height: $header-height;
    @include font-size(2.4rem, round-float(30/24, 2));
    color: #e7dad2;
    text-decoration: none;

    @include media('<desk-s') {
      @include padding-horizontal(15px); }

    @include media('<lap') {
      font-size: 2rem; }

    @include media('<lap-s') {
      @include padding-horizontal(8px);
      font-size: 1.8rem; }

    @include media('<tab') {
      color: $text-color;
      @include font-medium;
      @include font-size(3.6rem, round-float(48/36, 2));
      padding: 9px 20px;
      text-align: center;
      justify-content: center;
      height: auto; } }

  // hover
  .no-touchevents & {
    a:hover {
      box-shadow: inset 0 -2px 0 $base-color;

      @include media('<tab') {
        box-shadow: none; } } }

  // current
  li.current {
    a {
      color: white;
      background-color: #c4161c;

      @include media('<tab') {
        background-color: transparent;
        color: $base-color; } } } }

.nav-footer {
  position: relative;
  display: none;

  @include media('<tab') {
    display: flex;
    width: 100%;
    min-height: $header-height; }

  &::before {
    border-top-width: 1px; } }

/* ==================================== */
/* ============= FOOTER =============== */
/* ==================================== */
.footer {
  position: relative; }

.footer__holder {
  overflow: hidden;
  cursor: default;
  padding: $footer-grid-space;
  border: solid #5a534f;
  border-width: 1px 0 0; }

/* footer bottom */
.footer-bottom {
  position: relative;
  // обрезать вытянутый маржинами по горизонтале дочерний элемент
  overflow: hidden; }

.footer-bottom__holder {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  @include margin-horizontal(-#{round(nth($grid-space, 2)/2)});
  margin-bottom: -#{round(nth($grid-space, 1)/2)}; }

.footer-bottom__col {
  text-align: center;
  @include padding-horizontal(round(nth($grid-space, 2)/2));
  margin-bottom: #{round(nth($grid-space, 1)/2)};
  // для браузеров не поддерживающих display: flex
  display: inline-block;
  vertical-align: top;
  width: calc(33.33%);

  @include media('<mob') {
    min-width: 100%; }

  &:first-child {
    @include media('>=mob') {
      text-align: left; } }

  &:last-child {
    @include media('>=mob') {
      text-align: right; } }

  a {
    color: inherit;
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
  display: inline-block;
  min-height: 25px;

  a {
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: none; } } }

.developer__label {
  @include media('<mob') {
    display: block; }

  i {
    @include media('<mob') {
      display: none; } } }

.developer__logo {
  @include display-inline-block-top;
  position: relative;
  top: 5px;
  padding-right: 3px;
  @include size(154px, 20px);
  text-align: left;
  text-indent: -9999px;
  color: white;

  &::before, &::after {
    @include pseudo();
    @include position(0, 0);
    @include size(149px, 16px); }

  &::before {
    @include pseudo();
    @include position(4px, 4px);
    background: url('data:image/png;base64,');
    background-size: 2px; }

  .no-touchevents a:hover {
    transform: translate(0, 1px);
    filter: blur(2px);
    opacity: 0.6; }

  &::after {
    @include pseudo();
    background: #cc373c url('data:image/png;base64,') no-repeat center center;
    background-size: auto 16px; } }

// hover
.no-touchevents a:hover {
  .developer__logo {
    color: black;

    &::after {
      background-color: #00ef85; } } }

/* developer end */

/* ==================================== */
/* ============== MAIN ================ */
/* ==================================== */

.main {
  position: relative;
  @include padding-vertical(75px, 50px);

  @include media('<tab') {
    @include padding-vertical(50px, 40px); }

  @include media('<tab-s') {
    @include padding-vertical(40px); }

  @include media('<mob') {
    @include padding-vertical(30px); }

  .sep-page & {
    padding: 0; } }

/* products */
.products {
  // хорошо получается с flex, но не работает в ie10
  //display: flex
  //justify-content: center
  position: relative;
  margin-bottom: 5rem;
  text-align: center;

  &:last-child {
    margin-bottom: 0; } }

.products__list {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin-top: -3rem;
  text-align: left;
  @include clear-spacing;

  @include media('<tab-s') {
    justify-content: flex-start; } }

.products__item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 320px;
  width: calc(100% / 5);
  margin-top: 4rem;
  color: inherit;
  text-decoration: inherit;
  letter-spacing: normal;
  font-size: 1.6rem;

  @include media('<1920px') {
    width: calc(100% / 4); }

  @include media('<1600px') {
    width: calc(100% / 3); }

  @include media('<1280px') {
    width: calc(100% / 2); }

  @include media('<992px') {
    width: calc(100% / 3);
    min-width: 0; }

  @include media('<900px') {
    width: calc(100% / 2); }

  @include media('<mob') {
    width: calc(100% / 3); }

  @include media('<mob-s') {
    width: calc(100% / 2); } }

.products__item__inner {
  display: block;
  position: relative;
  color: inherit;
  text-decoration: inherit;
  min-height: 590px;
  z-index: 1;

  .no-touchevents {
    min-height: 0; }

  @include media('<tab') {
    min-height: 0; } }

.products__item__img {
  position: relative;
  box-shadow: 0 15px 50px rgba(black, 0.2);
  width: 194px;
  max-width: 90%;
  margin: 0 auto 2rem;
  z-index: 1;

  @include media('<tab') {
    width: 156px; }

  @include media('<mob') {
    width: 80px;
    box-shadow: 0 10px 25px rgba(black, 0.25); } }

.products__item__awards {
  position: absolute;
  right: 0;
  top: 20px;
  width: 50px;
  margin: 0 -25px;

  @include media('<tab') {
    width: 38px;
    margin: 0 -19px; }

  @include media('<mob') {
    width: 30px;
    margin: 0 -15px; }

  img {
    width: 100%;
    margin-top: -25px;

    @include media('<tab') {
      margin-top: -19px; }

    @include media('<mob') {
      margin-top: -15px; }

    &:first-child {
      margin-top: 0; } } }

.products__item__title {
  @include font-size(2.4rem, round-float(30/24, 2));
  text-align: center;
  width: 260px;
  max-width: 100%;
  @include padding-horizontal(15px);
  margin: 0 auto;
  z-index: -1;
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: 0.33s; }

  @include media('<mob') {
    @include font-size(1.8rem, round-float(20/18, 2)); } }

.products__item__info {
  position: absolute;
  left: 0;
  top: 200px;
  width: 100%;
  background-color: $base-color;
  padding: 240px 15px 40px;
  @include font-light;
  @include font-size(1.8rem, round-float(24/18, 2));
  color: white;
  transform: translateY(-20px);
  opacity: 0;
  $prop: transform, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: 0.33s; }

  .touchevents & {
    display: none; }

  @include media('<tab') {
    top: 160px;
    padding-top: 195px; }

  @include media('<mob') {
    top: 80px;
    padding: 105px 10px 15px;
    font-size: 1.6rem; } }

.info-list {
  &, .user-content & {
    margin: 0 auto;
    padding: 0;

    li {
      padding-left: 0;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0; }

      &::before {
        content: none; } } }

  & {
    max-width: 220px; }

  li {
    position: relative;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      position: absolute;
      left: 5px;
      right: 5px;
      bottom: 5px;
      //border-top: 1px dotted white
      height: 1px;
      background: url(../../img/dot.png);
      opacity: 0.6; }

    & > div {
      max-width: 50%;
      position: relative;
      display: inline-block;
      vertical-align: top;
      background-color: $base-color;
      @include padding-horizontal(10px);
      z-index: 1;

      &:first-child {
        padding-left: 0;
        float: left; }

      &:last-child {
        padding-right: 0;
        float: right; } } } }

// hover
.no-touchevents .products__item:hover {
  z-index: 2;

  .products__item__title {
    opacity: 0;
    transform: translateY(40px); }

  .products__item__info {
    opacity: 1;
    transform: translateY(0); } }

/* products end */

/* product */
.prod {
  width: 100%;
  height: 100%;
  position: relative; }

.prod-descr {
  width: 50%;
  height: 100%;
  display: inline-block;
  @include font-medium; }

.prod-image {
  width: calc(50% + #{nth($layout-page-space, 2)});
  margin-right: -#{nth($layout-page-space, 2)};
  position: relative;
  position: sticky;
  overflow: hidden;
  top: $header-height;
  float: right;

  @include media('<lap-s') {
    width: calc(50% + #{nth($layout-page-space--md, 2)});
    margin-right: -#{nth($layout-page-space--md, 2)}; }

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: url(../../img/bg-img.png) repeat 0 34px; }

  .no-csspositionsticky & {
    top: auto; } }

.prod-wrap, .image-wrap {
  padding: 75px 160px;

  @include media('<desk') {
    @include padding-horizontal(100px); }

  @include media('<desk-s') {
    @include padding-horizontal(80px); }

  @include media('<lap-s') {
    @include padding-horizontal(40px); }

  @include media('<tab') {
    padding: 50px 0 40px; } }

.prod-wrap {
  padding-left: 0; }

.image-wrap {
  padding-right: 0;
  padding-left: 0;
  min-height: calc(100vh - #{$header-height});

  figure {
    margin: 0;
    padding: 0;
    box-shadow: 0 25px 50px rgba(black, 0.5);
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 290px;

    @include media('<lap') {
      width: 220px; }

    img {
      width: 100%; } } }

.prod-circle {
  height: 500px;
  width: 500px;
  border-radius: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include media('<lap') {
    width: 380px;
    height: 380px; } }

.prod-prev, .prod-next {
  position: absolute;
  width: 120px;
  top: 50%;
  text-decoration: none;
  color: currentColor;
  z-index: 10;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  $prop: opacity, visibility;
  opacity: 0.5;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  @include media('<lap-s') {
    width: 100px; }

  @include media('<tab') {
    @include size(90px);
    opacity: 1; }

  @include media('<mob-s') {
    @include size(60px, 100px); }

  img {
    position: relative; }

  svg {
    display: block;
    @include size(30px);
    position: absolute;
    top: 50%;
    margin: -15px 50px 0;
    pointer-events: none;
    $prop: transform, opacity, visibility;
    opacity: 0;
    visibility: hidden;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    @include media('<desk-s') {
      @include margin-horizontal(20px); }

    @include media('<tab') {
      opacity: 1;
      visibility: visible;
      @include margin-horizontal(-15px); } }

  path {
    fill: currentColor; } }

.prod-prev {
  left: 0;
  transform: translate(-50%, -50%);

  @include media('<tab') {
    transform: translate(0, -50%); }

  svg {
    left: 100%;
    transform: rotate(0.25turn);

    @include media('<tab') {
      left: 50%; } } }

.prod-next {
  right: 0;
  transform: translate(50%, -50%);

  @include media('<tab') {
    transform: translate(0, -50%); }

  svg {
    right: 100%;
    transform: rotate(-0.25turn);

    @include media('<tab') {
      right: 50%; } } }

// hover
.prod-prev, .prod-next {
  .touchevents &, .no-touchevents &:hover {
    opacity: 1; } }

.prod-prev {
  .touchevents &, .no-touchevents &:hover {
    svg {
      opacity: 1;
      visibility: visible; } } }

.prod-next {
  .touchevents &, .no-touchevents &:hover {
    svg {
      opacity: 1;
      visibility: visible; } } }

// products awards
.prod-icons {
  padding-bottom: 40px;

  img {
    display: inline-block;
    width: 50px;
    height: 50px; } }

.prod-descr {
  h2 {
    margin-bottom: 4rem;
    font-size: 6.0rem;
    line-height: round-float(72/60, 2);

    @include media('<lap') {
      font-size: 4.2rem; }

    @include media('<tab') {
      margin-bottom: 3rem;
      font-size: 3.6rem; } }

  p {
    @include font-normal;
    font-size: 2.2rem;
    line-height: round-float(30/24, 2);

    @include media('<tab') {
      font-size: 2rem; } }

  h3 {
    font-size: 3.6rem;
    line-height: round-float(40/36, 2);

    @include media('<tab') {
      font-size: 2.4rem; } }

  table {
    //display: block
    width: 100%;
    margin-bottom: 3rem;
    font-size: 2.2rem;
    line-height: round-float(30/24, 2);
    @include font-normal;

    @include media('<tab') {
      font-size: 2rem; }

    &:last-child {
      margin-bottom: 0; }

    //tbody
    //display: block

    tr {
      //display: block
      //position: relative
      //overflow: hidden
      //margin-bottom: 20px
      //z-index: 1

      &:last-child {
        margin-bottom: 0; } }

    //&::before
    //content: ""
    //display: block
    //position: absolute
    //left: 5px
    //right: 5px
    //bottom: 7px
    //height: 1px
    //background: url(../../img/dot.png)
    //z-index: -1
    //filter: invert(1)
    //opacity: 0.4

    td {
      $th-right-width: 200px;
      //display: block
      //padding-bottom: 20px
      //background-color: $bg-color
      padding-bottom: 20px;
      vertical-align: bottom;

      tr:last-child & {
        padding-bottom: 0; }

      &:first-child {
        //float: left
        padding-right: 13px;
        text-align: left; }
      //max-width: calc(100% - #{$th-right-width})

      &:last-child {
        //float: right
        text-align: right;
        padding-left: 13px;
        min-width: $th-right-width; } } } }

.prod-descr-circle {
  @include size(280px);
  border-radius: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.prod-descr-circle.green,
.prod-circle.green {
  background: #335346; }

.prod-descr-circle.blue,
.prod-circle.blue {
  background: #39485d; }

.prod-descr-circle.red,
.prod-circle.red {
  background: #654349; }

.prod-descr-image {
  display: none;
  position: relative;
  margin: 0 -#{nth($layout-page-space--sm, 2)} 3rem;
  overflow: hidden;

  @include media('<mob') {
    @include margin-horizontal(-#{nth($layout-page-space--mob, 2)}); }

  figure {
    position: relative;
    padding: 0;
    margin: 0 auto;
    width: 160px; } }

@include media('<tab') {

  .prod-descr-image {
    display: block; }

  .prod-image {
    display: none; }

  .prod-descr {
    width: 100%; } }

/* product end */

/* scene */
.scene {
  position: fixed;
  overflow: hidden;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  //z-index: -1
  z-index: 95;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  pointer-events: none;

  .sep-page & {
    position: absolute;
    top: 0;
    z-index: auto; }

  .touchevents & {
    display: none; }

  @include media('<tab') {
    display: none; }

  & > div {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

    img {
      position: absolute; }

    @include media('<tab') {
      &:nth-child(2) {
        img {
          left: auto !important;
          top: 38vh !important;
          right: 40px; } }

      &:nth-child(3) {
        img {
          left: auto !important;
          top: 45vh !important;
          right: -10px; } } } } }

/* scene end */

/* slider arrow */
.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 50%;
  width: 48px;
  height: 48px;
  background: url(../../img/carousel-arr.png) no-repeat;
  margin: -24px 0 0;
  padding: 0;
  border: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  visibility: visible;
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
  outline: none; }

.swiper-button-prev:active,
.swiper-button-next:active {
  -webkit-transform: scale(0.96);
  -ms-transform: scale(0.96);
  transform: scale(0.96); }

.swiper-button-prev {
  left: 20px;
  background-position: 0 0; }

.swiper-button-prev:hover {
  background-position: 0 -68px; }

.swiper-button-next {
  right: 20px;
  background-position: -68px 0; }

.swiper-button-next:hover {
  background-position: -68px -68px; }

/* slider arrow end */

/* promo slider */
.main-slider {
  margin-bottom: 4rem;

  &:last-child {
    margin-bottom: 0; } }

.promo-slider {
  max-width: 100rem;
  //max-width: $layout-page-max-width
  margin: 0 auto;
  box-sizing: border-box;
  border-radius: 10px;
  //border: 4px solid #e9cd78
  background-color: #fff;
  border: 1px solid #e9cd78;
  box-shadow: 0 0 0 3px #fff, 0 0 0 4px #e9cd78;

  &.swiper-container-horizontal {
    margin-bottom: 0; } }

.content .promo-slider {
  .swiper-wrapper, .swiper-slide {
    margin-bottom: 20px; }

  .swiper-wrapper {
    align-items: center; } }

.promo-slider {
  .swiper-slide {
    box-sizing: border-box;
    overflow: hidden; }

  a {
    display: block;
    text-decoration: none;
    color: currentColor; }

  img {
    width: 100%; }

  .swiper-button-prev {
    left: 30px; }

  .swiper-button-next {
    right: 30px; } }

/* promo slider end */

/* footer news */
.footerNews {
  //height: 204px
  margin: 0 auto 20px;
  position: relative;
  //height: 259px
  min-height: 259px;
  width: 977px;
  max-width: 100%;
  padding: 55px 0 0 0;
  background: url("../../i/footer-news.png") no-repeat 0 0;
  background-size: 100% 100%;

  @include media('<tab') {
    background: white;
    border: 1px solid $base-color-alt;
    box-shadow: 0 0 0 3px #fff, 0 0 0 4px $base-color-alt;
    border-radius: 10px;
    padding: $grid-space; }

  @include media('<tab-s') {
    padding: $grid-space--tab; }

  @include media('<mob') {
    padding: $grid-space--mob; } }

.footerNews:after {
  content: "";
  clear: both;
  display: block; }

.footerNewsTitle {
  width: 120px;
  height: 20px;
  background: url("../../i/footer-news-title.png") no-repeat 0 0;
  overflow: hidden;
  margin: 0 0 20px; }

.footerNewsTitle a {
  display: block;
  height: 20px;
  width: 115px;
  background: url(../../i/blank.gif); }

.footerNews .shortNews {
  float: left;
  width: 190px;
  padding: 0 20px 0 0;
  color: #7d5822;
  //font: 14px/18px Cambria, "CambriaRegularWebfont", "Times New Roman"
