      background: url("../../i/promo-video-play.png") no-repeat 0 43px; } } }

.promoVideo a:hover {
  background-position: 100% 43px;

  @include media('<tab') {
    &::before {
      background-position: 100% 43px; } } }

/* promo video end */

/* b-legend */
.b-legend {
  //font: 15px/1.4 Cambria, "CambriaRegularWebfont", "Times New Roman"
  font-size: 15px;
  line-height: 1.4;
  color: #7d5822;
  padding: 0 50px 30px;

  @include media('<tab') {
    padding: 0;
    margin-bottom: 30px; } }

/* b-legend end */

/* b-prlist */
.b-prlist {
  padding-bottom: 20px; }

.b-prlist a {
  text-decoration: none; }

.b-prlist a:hover {
  text-decoration: underline; }

.b-prlist ul {
  display: table;
  width: 100%;
  width: calc(100% - 50px);
  table-layout: fixed;
  border-top: 1px dotted #ddcba9;
  border-bottom: 1px dotted #ddcba9;
  margin: 0 auto;

  @include media('<tab') {
    display: flex;
    flex-wrap: wrap;
    width: auto;
    margin: 0 -10px;
    padding: #{round-float(nth($grid-space, 1) / 2, 1)} 0; } }

.b-prlist li {
  display: table-cell;
  padding: 30px 5px 20px 5px;

  @include media('<tab') {
    display: block;
    flex-shrink: 0;
    $width: 33.3%;
    flex-basis: $width;
    width: $width;
    padding: #{round-float(nth($grid-space, 1) / 2, 1)} 10px; }

  @include media('<tab-s') {
    $width: 50%;
    flex-basis: $width;
    width: $width; } }

.b-prlist b {
  display: block;
  float: left;
  border-radius: 7px;
  border: 2px solid #e9cd78;
  padding: 3px;
  margin-right: 10px;
  background: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2); }

.b-prlist i {
  display: block;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  -webkit-background-size: contain;
  background-size: contain; }

.b-prlist span {
  display: block;
  overflow: hidden;
  color: #7d5822;
  font-size: 13px;
  font-weight: 600;
  line-height: 16px; }

.b-prlist li a {
  color: #7d5822; }

/* b-prlist end */
